import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';

/**
 * Generated class for the NicknameUpdatePage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-nickname-update',
  templateUrl: 'nickname-update.html',
})
export class NicknameUpdatePage {

  nickname : any ;
  constructor(public navCtrl: NavController, public navParams: NavParams , public viewCtrl : ViewController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad NicknameUpdatePage');
  }

  cancel()
  {
    this.viewCtrl.dismiss();
  }

  dismiss()
  {

    let data = { 'nickname': this.nickname };
    console.log("data send is "+ data);
    console.log(this.nickname);
    this.viewCtrl.dismiss(data);
  }
  

}



